:root {
  /*
  Table of contents (for variables):
      1.  Base variables ------------------------------------------- [hmv-01]
      2.  System colors -------------------------------------------- [hmv-02]
      3.  Context colors ------------------------------------------- [hmv-03]
      4.  Box shadows ---------------------------------------------- [hmv-04]
