  background: var(--dm-progress-bar-animated-highlight-dark-bg-color);
}

@-webkit-keyframes progress-bar-shine {
  to {
    transform: translateX(0);
    opacity: 0.1;
  }
}

@keyframes progress-bar-shine {
  to {
    transform: translateX(0);
